// BookDetail.js
import React, { useEffect, useState } from 'react';
import {useParams } from 'react-router-dom';
import axios from 'axios';
import DefaultLayout from '../components/DefaultLayout';
import "../resources/bookdetail.css"
import { PlusOutlined,EditOutlined, DeleteOutlined } from '@ant-design/icons';
import { Rate } from 'antd';


function BookDetail() {
    const { id } = useParams();
    const [book, setBook] = useState(null);
    const [reviews, setReviews] = useState([]);
    const [newReview, setNewReview] = useState('');
    const [user, setUser] = useState(null);
    const [uniqueCategories, setUniqueCategories] = useState([]);
    const [isDescriptionExpanded, setIsDescriptionExpanded] = useState(false);
    const [isReviewFormVisible, setIsReviewFormVisible] = useState(false);
    const [existingReview, setExistingReview] = useState(null);
    const [rating, setRating] = useState(0);

    const API_KEY = 'AIzaSyCuQglxyWQcGZv-ilI4AAvw39tVjHzwbRs';

    useEffect(() => {
        fetchBookDetail();
        fetchReviews();
        const loggedUser = localStorage.getItem('bookverse-user');
        if (loggedUser) {
            setUser(JSON.parse(loggedUser)); 
        }
    }, [id]);

    const fetchBookDetail = async () => {
        try {
            const response = await axios.get(`https://www.googleapis.com/books/v1/volumes/${id}?key=${API_KEY}`);
            setBook(response.data);
            console.log(response.data);
            fetchAdditionalBookDetails(response.data.selfLink);
        } catch (error) {
            console.error('Error fetching book detail:', error);
        }
    };

    const fetchAdditionalBookDetails = async (selfLink) => {
        try {
            const response = await axios.get(`${selfLink}?key=${API_KEY}`);
            const { pageCount, categories, maturityRating } = response.data.volumeInfo;
            if (categories) {
                // Split categories by both '/' and ',' and then store unique ones
                const splitCategories = categories.flatMap(category => category.split(/\/|,/).map(str => str.trim()));
                const uniqueCategoriesSet = new Set(splitCategories);
                setUniqueCategories(Array.from(uniqueCategoriesSet));
                // uniqueCategories.forEach(category => console.log(category));
            }
        } catch (error) {
            console.error('Error fetching additional book details:', error);
        }
    };

    const fetchReviews = async () => {
        try {
            const response = await axios.get(`/api/reviews/${id}`);
            setReviews(response.data);
            if (user) {
                const userReview = response.data.find(review => review.username === user.data.username);
                if (userReview) {
                    setExistingReview(userReview);
                    setNewReview(userReview.comment); // Pre-fill the form if the user has a review
                }
            }
        } catch (error) {
            console.error('Error fetching reviews:', error);
        }
    };

    const handleReviewSubmit = async (event) => {
        event.preventDefault();
        if (!user) {
            console.error('User is not logged in.');
            return;
        }
        try {
            console.log(user);
            await axios.post('/api/reviews/add', { bookId: id, username:user.data.username, comment: newReview, rating: rating });
            console.log(user.data.username);
            setNewReview('');
            setIsReviewFormVisible(false);
            fetchReviews();
        } catch (error) {
            console.error('Error submitting review:', error);
        }
    };

    if (!book) return <div>Loading...</div>;

    const truncateText = (text, wordLimit) => {
        const words = text.split(' ');
        if (words.length > wordLimit) {
            return words.slice(0, wordLimit).join(' ') + '....';
        }
        return text;
    };

    const handleReadMoreClick = () => {
        setIsDescriptionExpanded(!isDescriptionExpanded);
    };

    const handleAddReviewClick = () => {
        setIsReviewFormVisible(!isReviewFormVisible);
    };

    const handleRatingChange = (value) => {
        setRating(value);
        console.log('Rating:', value); // Log the selected rating
    };



    const { volumeInfo } = book;
    const imageUrl= volumeInfo.imageLinks?.thumbnail || volumeInfo.imageLinks?.smallThumbnail ||'';
   
    return (

        <DefaultLayout>
            <div className='book-detail'>
                <div className='book-detail-content-wrapper'>
                    <div className='book-detail-image'>
                        <img src={imageUrl} alt={volumeInfo.title} />
                    </div>
                    <div className='book-detail-content'>
                        <h4 className='book-detail-heading'>{volumeInfo.title}</h4>
                        <h6 className='book-detail-author'>
                        {volumeInfo.authors && volumeInfo.authors.map((author, index) => (
                            <React.Fragment key={index}>
                                {author}
                                {index < volumeInfo.authors.length - 1 && <br />}
                            </React.Fragment>
                        ))}
                        </h6>
                        <div className='book-detail-categories'>
                            {uniqueCategories.map((category, index) => (
                                <div key={index} className='book-detail-category-box'>
                                    {category}
                                </div>
                            ))}
                        </div>
                        <div className='book-detail-description'>
                            {isDescriptionExpanded ? (
                                <div dangerouslySetInnerHTML={{ __html: volumeInfo.description }}></div>
                            ) : (
                                <div dangerouslySetInnerHTML={{ __html: truncateText(volumeInfo.description, 70) }}></div>
                            )}
                            <button className='read-more-button' onClick={handleReadMoreClick}>
                                {isDescriptionExpanded ? 'Show less ' : 'Read more '}
                            </button>
                        </div>
                    </div>
                </div>
                <div className='reviews-section'>
                    <h3>Reviews</h3>
                    <div className='add-review-container'>
                        <PlusOutlined
                            className='add-review-icon'
                            onClick={handleAddReviewClick}
                        />
                        <span className='add-review-text'>Add Review</span>
                    </div>
                    
                    {isReviewFormVisible && !existingReview && (
                        <form onSubmit={handleReviewSubmit}>
                            <div className='rate-container'>
                                <Rate onChange={handleRatingChange} value={rating} />
                            </div>

                            <textarea
                                value={newReview}
                                onChange={(e) => setNewReview(e.target.value)}
                                placeholder='Write a review...'
                                className='review-input'
                            ></textarea>
                            <button type='submit' className='review-button'>Submit</button>
                        </form>
                    )}
                    {isReviewFormVisible && existingReview && (
                        <div className='review existing-review'>
                            <div className='review-username'>Your Review</div>
                            <div className='existing-review-rating'>
                                <Rate disabled value={rating} /> 
                            </div>
                            <div>{existingReview.comment}</div>
                            <div className='review-actions'>
                                <EditOutlined className='edit-icon' />
                                <DeleteOutlined className='delete-icon' />
                            </div>
                        </div>
                    )}

                    {reviews.map((review) => (
                        <div key={review._id} className='review'>
                            <div className='existing-review-rating'>
                                <Rate disabled value={review.rating} /> 
                            </div>
                            <div className='review-username'>{review.username}</div>
                            {review.comment}
                        </div>
                    ))}
                </div>
            </div>
        </DefaultLayout>
    );
}

export default BookDetail;
