import React from 'react'
import Nav from './Nav'
import "../resources/default.css"

function DefaultLayout(props) {
  return (
    <div className='layout'>
        <div className='header'>
            <div>
                {/* <h1 className='logo'>THE SG</h1> */}
                <Nav/>
            </div>
        </div>

        <div className='content'>
            {props.children}
        </div>

    </div>
  )
}

export default DefaultLayout