import React, { useEffect, useState } from 'react';
import '../resources/books.css';
import axios from 'axios';
import DefaultLayout from '../components/DefaultLayout';
import { useNavigate } from 'react-router-dom';

function Books() {
  const [books, setBooks] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const API_KEY = 'AIzaSyCuQglxyWQcGZv-ilI4AAvw39tVjHzwbRs'; // Replace with your actual API key
    const DEFAULT_SEARCH_TERM = 'bestsellers 2023'; // Default search term

    const navigate = useNavigate();

    useEffect(() => {
        fetchBooks(DEFAULT_SEARCH_TERM);
    }, []);

    const fetchBooks = async (term) => {
        setBooks([]);
        try {
            const response = await axios.get(`https://www.googleapis.com/books/v1/volumes?q=${term}&maxResults=10&key=${API_KEY}`);
            setBooks(response.data.items || []); // Ensure books is set to an empty array if no items
        } catch (error) {
            console.error('Error fetching book data:', error);
        }
    };

    const handleSearch = (event) => {
        event.preventDefault();
        if (searchTerm.trim()) { // Only search if the search term is not empty
            fetchBooks(searchTerm.trim());
            setSearchTerm(''); // Clear the search input after submitting
        }
    };

    const handleInputChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const truncateText = (text, wordLimit) => {
        const words = text.split(' ');
        if (words.length > wordLimit) {
            return words.slice(0, wordLimit).join(' ') + '....';
        }
        return text;
    };

    const handleBookClick = (bookId) => {
        navigate(`/book/${bookId}`);
    };

  return (
    <DefaultLayout>
        <div style={{display:"flexColumn", backgroundColor:"rgba(51,51,51)"}}>
        <div className='books'>
            <form  className='search-form'onSubmit={handleSearch}>
                <input className='search-input'
                    type='text'
                    placeholder='Search for books...'
                    value={searchTerm}
                    onChange={handleInputChange}
                />
                <button type='submit' className='search-button'>Search</button>
            </form>
            {books.length === 0 ? (
                <div></div>
            ) : (
                books.map((book) => {
                    const { volumeInfo } = book;
                    const imageUrl = volumeInfo.imageLinks?.extraLarge || volumeInfo.imageLinks?.large || volumeInfo.imageLinks?.medium || volumeInfo.imageLinks?.thumbnail || volumeInfo.imageLinks?.smallThumbnail ||'';
                    console.log(imageUrl);
                    return (
                        <div className='book' key={volumeInfo.title} onClick={() => handleBookClick(book.id)}>
                            <div className='book-image'>
                                <img src={imageUrl} alt={volumeInfo.title} />
                            </div>
                            <div className='book-content'>
                                <h6>{volumeInfo.title}</h6>
                                <p>{truncateText(volumeInfo.description || 'No description available.', 15)}</p>
                            </div>
                        </div>
                    );
                })
            )}
        </div>
        </div>
    </DefaultLayout>
  )
}

export default Books
