import React,{ useState, useEffect } from 'react'
import "../resources/nav.css"
import { Link } from 'react-router-dom';

function Nav() {
const[show,handleShow]=useState(false);
const transitionNavBar=()=>{
    if(window.scrollY>100){
        handleShow(true);
    }
    else{
        handleShow(false);
    }
}

useEffect(()=>{
    window.addEventListener("scroll",transitionNavBar);
    return () =>window.removeEventListener("scroll",transitionNavBar);
},[]);

  return (
    <div className={`nav ${show && "nav_black"}`}>
        <div className='nav_contents'>
        <Link to="/books" className='nav_link'>
        <img alt="nav_logo" 
            style={{height: "40px",width: "40px"}} 
            src="https://assets.thestorygraph.com/assets/logo-no-text-dark-mode-c6312775a773a77605dd659850b4c0cd08db5c2044ef53a4f7b5186f8ca05f1f.png" 
            />
        </Link>
            {/* <h5 className='h3-tag' style={{margin:"0px"}}>Home</h5>
            <h5 className='h3-tag' style={{margin:"0px"}}>Stats</h5>
            <h5 className='h3-tag' style={{margin:"0px"}}>Communities</h5>
            <h5 className='h3-tag' style={{margin:"0px"}}>Giveaway</h5>
            <h5 className='h3-tag' style={{margin:"0px"}}>Search</h5> */}

<Link to="/books" className='nav_link'>
          <h5 className='h3-tag' style={{ margin: "0px" }}>Home</h5>
        </Link>
        <Link to="/stats" className='nav_link'>
          <h5 className='h3-tag' style={{ margin: "0px" }}>Stats</h5>
        </Link>
        <Link to="/communities" className='nav_link'>
          <h5 className='h3-tag' style={{ margin: "0px" }}>Communities</h5>
        </Link>
        <Link to="/giveaway" className='nav_link'>
          <h5 className='h3-tag' style={{ margin: "0px" }}>Giveaway</h5>
        </Link>
        <Link to="/search" className='nav_link'>
          <h5 className='h3-tag' style={{ margin: "0px" }}>Search</h5>
        </Link>
        </div>
    </div>
  )
}

export default Nav