import React from 'react'
import '../resources/signup.css'
import { Button,Form, Input, message,  } from 'antd'
import { Link, Navigate, useNavigate } from 'react-router-dom'
import axios from 'axios'

function Signup() {

  const navigate=useNavigate()

  const onFinish=async(values)=>{
    try{
      await axios.post('/api/users/signup',values)
      message.success('Registration Succesful')
      navigate('/login')
    }
    catch(error){
      message.error('Something went wrong')
    }
  }

  return (
    <div className='signup-screen'>
      <div className='signup-screen-bg'>
        <img className='signup-screen-img' src="\images\signupbg.png" alt=""/>
      </div>
      <div>
        <div className='form-container'>
          <h1 style={{fontWeight:"600", marginBottom:"4vh"}}>SignUp</h1>
          <Form  className="register-form"layout='vertical' onFinish={onFinish} >
            <Form.Item label='Username' name='username'>
              <Input/>
            </Form.Item>
            <Form.Item label='Email' name='email'>
              <Input/>
            </Form.Item>
            <Form.Item label='Password' name='password'>
              <Input/>
            </Form.Item>
            <div className="d-flex justify-content flex-column align-items-center">
              <button className='submit-button' type="submit">Submit</button>
              <Link to='/login'>Already have an account? Click here to Login</Link>
            </div>
          </Form>
        </div>
      </div>
    </div>
  )
}

export default Signup