import React from 'react'
import '../resources/signup.css'
import { Button,Form, Input, message,  } from 'antd'
import { Link, useNavigate } from 'react-router-dom'
import axios from 'axios'

function Login() {

  const navigate=useNavigate()

  const onFinish=async (values)=>{
    try{
      const response=await axios.post('/api/users/login',values)
      const user = response;
      localStorage.setItem('bookverse-user',JSON.stringify(user))
      message.success('Login succcessful')
      navigate('/books')
    }
    catch(error){
      message.error('Login failed')
    }
  }

  return (
    <div className='signup-screen'>
      <div className='signup-screen-bg'>
        <img className='signup-screen-img' src="\images\signupbg.png" alt=""/>
      </div>
      <div>
        <div className='form-container'>
          <h1 style={{fontWeight:"600", marginBottom:"4vh"}}>Login</h1>
          <Form  className="register-form" layout='vertical' onFinish={onFinish} >
            <Form.Item label='Email' name='email'>
              <Input/>
            </Form.Item>
            <Form.Item label='Password' name='password'>
              <Input/>
            </Form.Item>
            <div className="d-flex justify-content flex-column align-items-center">
              <button className='submit-button' type="submit">Submit</button>
              <Link to='/signup'>Don't have an account? Click here to SignUp</Link>
            </div>
          </Form>
        </div>
      </div>
    </div>
  )
}

export default Login